@import "assets/styles/_common.scss";

.home {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  position: relative;
  &::before {
    content: "";
    position: absolute;
    top: -25vh;
    left: -40vh;
    height: 100vh;
    width: 100vh;
    border-radius: 50%;
    background-color: $red-bean;
    z-index: -2;
  }
  &::after {
    content: "";
    position: absolute;
    top: -50vh;
    right: -60vh;
    height: 150vh;
    width: 142vh;
    border-radius: 50%;
    background-color: $red-coral;
    z-index: -1;
  }
  &-intro {
    width: 70%;
    height: 50vh;
    background-color: rgba($color: $red-light, $alpha: .5);
    border: 2px solid white;
    border-top-left-radius: 200px;
    border-bottom-right-radius: 200px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 25vh;
    position: relative;
    .x-factor {
      position: absolute;
      z-index: -1;
      top: -24%;
      left: 25%;
      width: 360px;
      height: 360px;
      &:before, &:after {
        position: absolute;
        left: 15px;
        content: ' ';
        height: 180px;
        width: 40px;
        background-color: $red-misty;
      }
      &:before {
        transform: rotate(45deg);
      }
      &:after {
        transform: rotate(-45deg);
      }
    }
    .o-factor {
      position: absolute;
      z-index: -1;
      bottom: -35%;
      right: 25%;
      width: 140px;
      height: 140px;
      border: 40px solid $red-misty;
      border-radius: 50%;
    }
  }
  &-avatar {
    width: fit-content;
    height: fit-content;
    border: 10px solid white;
    border-radius: 50%;
    margin-left: 10%;
  }
  &-greeting {
    margin-right: 10%;
    margin-left: 5%;
    h2 {
      font-size: 60px;
      font-weight: bold;
      margin-bottom: 20px;
      line-height: 1.2;
      span {
        color: $red-misty;
      }
    }
    p {
      font-size: 20px;
      line-height: 1.6;
    }
  }
}

@media screen and (max-width: 767px){
  .home {
    &::before {
      top: -26vh;
      left: -20vh;
      height: 50vh;
      width: 50vh;
    }
    &::after {
    top: unset;
    bottom: 5vh;
    right: -32vh;
    height: 46vh;
    width: 46vh;
    }
    &-intro {
      height: 80vh;
      width: 86%;
      margin-top: 14vh;
      flex-direction: column;
      justify-content: unset;
    }
    .x-factor {
      top: 0%;
      left: 10%;
      width: 120px;
      height: 120px;
      &:before, &:after {
        left: 15px;
        height: 100px;
        width: 25px;
      }
    }
    .o-factor {
      bottom: -2%;
      right: 4%;
      width: 80px;
      height: 80px;
      border-width: 24px;
    }
    &-avatar {
      border-width: 8px;
      width: 50vw;
      height: 50vw;
      margin-top: 20px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &-greeting {
      margin-right: 0;
      margin-left: 0;
      margin-top: 20px;
      padding: 24px;
      h2 {
        font-size: 32px;
        margin-bottom: 20px;
      }
      p {
        font-size: 16px;
      }
    }
  }
}